import React, { useState, useEffect, useRef, useCallback } from 'react';
import { useSearchParams, useNavigate } from 'react-router-dom';
import { Eye, Heart, Share2, ShoppingCart, Play, Volume2, VolumeX, ChevronLeft, ChevronRight, ArrowLeft, Check, ThumbsUp, UserPlus, UserMinus, Ticket, Copy, X } from 'lucide-react';
import { watchAPI, userAPI } from '../utils/api';
import { toast } from 'react-hot-toast';
import { useAuth } from '../context/AuthContext';

const Watch = () => {
  const [searchParams] = useSearchParams();
  const navigate = useNavigate();
  const { user, isAuthenticated } = useAuth();
  const [videos, setVideos] = useState([]);
  const [currentVideoIndex, setCurrentVideoIndex] = useState(0);
  const [loading, setLoading] = useState(true);
  const [videoRefs, setVideoRefs] = useState({});
  const [isPlaying, setIsPlaying] = useState(false);
  const [isMuted, setIsMuted] = useState(true);
  const [engagementCounts, setEngagementCounts] = useState({});
  const [likedItems, setLikedItems] = useState({});
  const [lovedItems, setLovedItems] = useState({});
  const [relatedProducts, setRelatedProducts] = useState([]);
  const [profileVideos, setProfileVideos] = useState([]);
  const [showProductModal, setShowProductModal] = useState(false);
  const [selectedProductData, setSelectedProductData] = useState(null);
  const [showShareModal, setShowShareModal] = useState(false);
  const [showStoreModal, setShowStoreModal] = useState(false);
  const [couponModalProduct, setCouponModalProduct] = useState(null);
  const [touchStart, setTouchStart] = useState(null);
  const [touchEnd, setTouchEnd] = useState(null);
  const [isTransitioning, setIsTransitioning] = useState(false);
  const [followingUsers, setFollowingUsers] = useState({}); // Track which users we're following { user_id: true/false }
  const [loadingFollow, setLoadingFollow] = useState({}); // Track loading state per user

  const videoContainerRef = useRef(null);
  const viewedVideos = useRef(new Set()); // Track which videos have been viewed

  // Create a stable ref callback to prevent infinite loops
  const setVideoRef = useCallback((index) => {
    return (el) => {
      if (el) {
        setVideoRefs(prev => {
          // Only update if the ref is different
          if (prev[index] !== el) {
            return { ...prev, [index]: el };
          }
          return prev;
        });
      }
    };
  }, []);

  useEffect(() => {
    const videoId = searchParams.get('video');
    console.log('Watch component mounted with video ID:', videoId);
    loadVideos(0, 20, true, videoId);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [searchParams]);

  useEffect(() => {
    if (videos.length > 0) {
      const currentVideo = videos[currentVideoIndex];
      const listingId = currentVideo?.listing_id || currentVideo?.video_id;
      
      // Track view when video becomes active (only once per video)
      if (listingId && !viewedVideos.current.has(listingId)) {
        viewedVideos.current.add(listingId);
        trackEngagement(listingId, 'view').then(() => {
          // Update view count in state
          setEngagementCounts(prev => ({
            ...prev,
            [listingId]: {
              ...prev[listingId],
              views: (prev[listingId]?.views || currentVideo.view_count || 0) + 1
            }
          }));
        }).catch(console.error);
      }
      
      // Load related products and videos for current video's provider
      if (currentVideo?.products && Array.isArray(currentVideo.products)) {
        setRelatedProducts(currentVideo.products);
      } else if (currentVideo?.provider_id) {
        loadRelatedProducts(currentVideo.provider_id);
        loadProfileVideos(currentVideo.provider_id, listingId);
      }
      
      // Pause all videos except current one
      Object.keys(videoRefs).forEach((key) => {
        const video = videoRefs[key];
        if (video && parseInt(key) !== currentVideoIndex) {
          video.pause();
        }
      });
      
      // Play current video
      const currentVideoEl = videoRefs[currentVideoIndex];
      if (currentVideoEl) {
        currentVideoEl.play().catch(console.error);
        setIsPlaying(true);
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentVideoIndex, videos]);

  const loadVideos = async (offset = 0, limit = 5, isInitialLoad = true, specificVideoId = null) => {
    try {
      console.log('Loading videos...');
      if (isInitialLoad) {
        setLoading(true);
      }
      
      let response;
      
      // If we have a specific video ID, try to get that video first
      if (specificVideoId && isInitialLoad) {
        try {
          const specificVideoResponse = await watchAPI.getVideoDetails(specificVideoId);
          if (specificVideoResponse.video) {
            // Get the specific video and then load the feed with admin priority
            const feedResponse = await watchAPI.getFeedWithAdminPriority(limit, offset);
            const allVideos = [specificVideoResponse.video, ...(feedResponse.videos || [])];
            
            // Remove duplicates based on listing_id
            const uniqueVideos = allVideos.filter((video, index, self) => 
              index === self.findIndex(v => v.listing_id === video.listing_id)
            );
            
            response = { videos: uniqueVideos };
            console.log(`Loading specific video ${specificVideoId} with admin priority feed`, response.videos);
          } else {
            response = await watchAPI.getFeedWithAdminPriority(limit, offset);
          }
        } catch (error) {
          console.log('Could not fetch specific video, falling back to admin priority feed:', error);
          response = await watchAPI.getFeedWithAdminPriority(limit, offset);
        }
      } else {
        response = await watchAPI.getFeedWithAdminPriority(limit, offset);
      }
      
      console.log(`Loading videos batch: offset=${offset}, limit=${limit}`, response.videos);
      
      // Filter videos that have valid video URLs and an identifier
      let validVideos = (response.videos || []).filter(video => 
        (video.listing_id || video.video_id) &&
        video.video_url && 
        video.video_url.trim() !== '' && 
        (video.video_url.startsWith('http') || video.video_url.startsWith('https'))
      );
      
      if (isInitialLoad) {
        console.log('Setting videos:', validVideos);
        setVideos(validVideos);
        
        // Load like statuses and follow statuses for all videos
        loadLikeStatuses(validVideos);
        loadFollowStatuses(validVideos);
        
        // Handle specific video ID from URL parameter
        if (specificVideoId) {
          const videoIndex = validVideos.findIndex(video => 
            (video.listing_id === parseInt(specificVideoId)) || (video.video_id === parseInt(specificVideoId))
          );
          console.log(`Looking for video ID ${specificVideoId} in videos:`, validVideos.map(v => v.listing_id));
          console.log(`Found video at index: ${videoIndex}`);
          if (videoIndex !== -1) {
            setCurrentVideoIndex(videoIndex);
            console.log(`Set current video index to: ${videoIndex}`);
          } else {
            // If specific video was requested but not found, set to 0 (first video)
            setCurrentVideoIndex(0);
            console.log('Specific video not found, set to index 0');
          }
        }
      } else {
        // Append new videos to existing list
        setVideos(prevVideos => [...prevVideos, ...validVideos]);
        
        // Load like statuses and follow statuses for new videos only
        loadLikeStatuses(validVideos);
        loadFollowStatuses(validVideos);
      }
      
      // Update engagement counts
      const counts = {};
      validVideos.forEach(video => {
        const id = video.listing_id || video.video_id;
        counts[id] = {
          views: video.view_count || 0,
          likes: video.like_count || 0,
          loves: video.love_count || 0,
          clicks: video.click_count || 0
        };
      });
      setEngagementCounts(prevCounts => ({ ...prevCounts, ...counts }));
      
      if (validVideos.length === 0 && isInitialLoad) {
        toast.error('No videos available');
      }
    } catch (error) {
      console.error('Error loading videos:', error);
      toast.error('Failed to load videos');
    } finally {
      if (isInitialLoad) {
        setLoading(false);
      }
    }
  };

  const loadRelatedProducts = async (providerId) => {
    if (!providerId) return;
    
    try {
      const response = await watchAPI.getProductsByProvider(providerId);
      setRelatedProducts(response.products || []);
    } catch (error) {
      console.error('Error loading related products:', error);
      setRelatedProducts([]);
    }
  };

  const loadProfileVideos = async (providerId, excludeListingId = null) => {
    if (!providerId) return;
    
    try {
      const response = await watchAPI.getVideosByProvider(providerId);
      // Filter out current video
      const filteredVideos = (response.videos || []).filter(
        video => video.listing_id !== excludeListingId
      );
      setProfileVideos(filteredVideos);
    } catch (error) {
      console.error('Error loading profile videos:', error);
      setProfileVideos([]);
    }
  };

  const openCouponModal = (product) => {
    if (product?.coupons?.length) {
      setCouponModalProduct(product);
    } else {
      toast.error('No coupons available for this product yet');
    }
  };

  const closeCouponModal = () => setCouponModalProduct(null);

  const handleCopyCoupon = async (code) => {
    try {
      await navigator.clipboard.writeText(code);
      toast.success('Coupon copied');
    } catch (error) {
      console.error('Failed to copy coupon:', error);
      toast.error('Failed to copy coupon');
    }
  };

  const renderCouponChips = (product, variant = 'dark') => {
    if (!product) return null;
    const coupons = Array.isArray(product.coupons) ? product.coupons : [];
    const isDark = variant === 'dark';

    const emptyClasses = isDark
      ? 'border-white/25 text-white/70 bg-transparent'
      : 'border-gray-200 text-gray-500 bg-gray-50';

    const chipClasses = isDark
      ? 'border-white/35 text-white bg-white/10 hover:bg-white/20'
      : 'border-gray-200 text-gray-700 bg-white hover:border-gray-300';

    const iconColor = isDark ? 'text-white/70' : 'text-gray-500';

    return (
      <div className="mt-2">
        {coupons.length === 0 ? (
          <span className={`inline-flex items-center px-3 py-1 text-[11px] font-medium rounded-full border ${emptyClasses}`}>
            No coupons yet
          </span>
        ) : (
          <div className="flex flex-wrap gap-1.5">
            {coupons.map((coupon, idx) => (
              <button
                key={`${coupon.code}-${idx}`}
                type="button"
                onClick={(e) => {
                  e.stopPropagation();
                  handleCopyCoupon(coupon.code);
                }}
                className={`inline-flex items-center gap-1.5 px-2.5 py-1 text-[11px] font-medium rounded-full border transition-colors ${chipClasses}`}
              >
                <span className="font-mono tracking-wide">{coupon.code}</span>
                <Copy className={`w-3 h-3 ${iconColor}`} />
              </button>
            ))}
          </div>
        )}
      </div>
    );
  };

  const trackEngagement = async (id, action) => {
    if (!id) return;
    
    try {
      await watchAPI.engage(id, action);
    } catch (error) {
      console.error(`Error tracking ${action}:`, error);
    }
  };

  const handleVideoClick = () => {
    const video = videoRefs[currentVideoIndex];
    if (video) {
      if (video.paused) {
        video.play();
        setIsPlaying(true);
      } else {
        video.pause();
        setIsPlaying(false);
      }
    }
  };

  const handleMuteToggle = () => {
    const video = videoRefs[currentVideoIndex];
    if (video) {
      video.muted = !video.muted;
      setIsMuted(video.muted);
    }
  };

  const handleEngagement = async (action) => {
    const currentVideo = videos[currentVideoIndex];
    if (currentVideo) {
      await trackEngagement(currentVideo.listing_id || currentVideo.video_id, action);
    }
  };


  const handleLove = async (id) => {
    console.log('Love button clicked for:', id);
    
    // Check if user is authenticated
    if (!isAuthenticated) {
      toast.error('Please log in to love videos');
      return;
    }
    
    try {
      const isCurrentlyLoved = lovedItems[id];
      
      // Optimistically update UI
      setLovedItems(prev => ({
        ...prev,
        [id]: !isCurrentlyLoved
      }));
      
      // Use watchAPI engage for love for videos table (id can be listing_id fallback)
      await watchAPI.engage(id, 'love');
      
      // Update count with actual response from server
      setEngagementCounts(prev => ({
        ...prev,
        [id]: {
          ...prev[id],
          loves: (prev[id]?.loves || 0) + (isCurrentlyLoved ? -1 : 1)
        }
      }));
      
      toast.success(isCurrentlyLoved ? 'Removed love' : 'Loved!');
    } catch (error) {
      console.error('Error toggling love:', error);
      
      // Revert optimistic update on error
      setLovedItems(prev => ({
        ...prev,
        [id]: !prev[id]
      }));
      
      // Handle different error types
      if (error.response?.status === 401) {
        toast.error('Please log in to love videos');
      } else if (error.response?.status === 500) {
        toast.error('Server error. Please try again.');
      } else {
        toast.error('Failed to update love');
      }
    }
  };

  const handleLike = async (id) => {
    console.log('Like button clicked for:', id);
    
    // Check if user is authenticated
    if (!isAuthenticated) {
      toast.error('Please log in to like videos');
      return;
    }
    
    try {
      const isCurrentlyLiked = likedItems[id];
      
      // Optimistically update UI
      setLikedItems(prev => ({
        ...prev,
        [id]: !isCurrentlyLiked
      }));
      
      // Use watchAPI like/unlike for videos table (id can be listing_id fallback)
      let response;
      if (isCurrentlyLiked) {
        response = await watchAPI.unlike(id);
      } else {
        response = await watchAPI.like(id);
      }
      
      // Update count with actual response from server
      setEngagementCounts(prev => ({
        ...prev,
        [id]: {
          ...prev[id],
          likes: response?.like_count ?? ((prev[id]?.likes || 0) + (isCurrentlyLiked ? -1 : 1))
        }
      }));
      
      toast.success(isCurrentlyLiked ? 'Removed from likes' : 'Added to likes');
    } catch (error) {
      console.error('Error toggling like:', error);
      
      // Revert optimistic update on error
      setLikedItems(prev => ({
        ...prev,
        [id]: !prev[id]
      }));
      
      // Handle different error types
      if (error.response?.status === 401) {
        toast.error('Please log in to like videos');
      } else if (error.response?.status === 500) {
        toast.error('Server error. Please try again.');
      } else {
        toast.error('Failed to update like');
      }
    }
  };

  const loadFollowStatuses = async (videos) => {
    // Only load follow statuses if user is authenticated
    if (!isAuthenticated) {
      console.log('User not authenticated, skipping follow status loading');
      return;
    }

    try {
      const uniqueUserIds = [...new Set(videos.map(v => v.user_id).filter(Boolean))];
      if (uniqueUserIds.length === 0) return;

      const followStatusPromises = uniqueUserIds.map(async (userId) => {
        try {
          const status = await userAPI.getFollowStatus(userId);
          return { userId, isFollowing: status.is_following || false };
        } catch (error) {
          console.error(`Error loading follow status for user ${userId}:`, error);
          return { userId, isFollowing: false };
        }
      });

      const followStatuses = await Promise.all(followStatusPromises);
      const newFollowingUsers = {};
      followStatuses.forEach(({ userId, isFollowing }) => {
        newFollowingUsers[userId] = isFollowing;
      });

      setFollowingUsers(prev => ({ ...prev, ...newFollowingUsers }));
    } catch (error) {
      console.error('Error loading follow statuses:', error);
    }
  };

  const handleFollowToggle = async (userId, e) => {
    if (e) {
      e.stopPropagation();
    }

    if (!isAuthenticated) {
      toast.error('Please log in to follow users');
      return;
    }

    if (!userId) {
      toast.error('User ID not available');
      return;
    }

    // Don't allow following yourself
    if (user && user.user_id === userId) {
      return;
    }

    const isCurrentlyFollowing = followingUsers[userId] || false;

    // Optimistically update UI
    setFollowingUsers(prev => ({
      ...prev,
      [userId]: !isCurrentlyFollowing
    }));

    setLoadingFollow(prev => ({ ...prev, [userId]: true }));

    try {
      if (isCurrentlyFollowing) {
        await userAPI.unfollow(userId);
        toast.success('Unfollowed');
      } else {
        await userAPI.follow(userId);
        toast.success('Followed');
      }
    } catch (error) {
      console.error('Error toggling follow:', error);
      
      // Revert optimistic update on error
      setFollowingUsers(prev => ({
        ...prev,
        [userId]: isCurrentlyFollowing
      }));

      if (error.response?.status === 401) {
        toast.error('Please log in to follow users');
      } else {
        toast.error('Failed to update follow status');
      }
    } finally {
      setLoadingFollow(prev => ({ ...prev, [userId]: false }));
    }
  };

  const loadLikeStatuses = async (videos) => {
    // Only load like/love statuses if user is authenticated
    if (!isAuthenticated) {
      console.log('User not authenticated, skipping like/love status loading');
      // Initialize counts from videos data
      const counts = {};
      videos.forEach(video => {
        counts[video.listing_id] = {
          likes: video.like_count || 0,
          loves: video.love_count || 0,
          clicks: video.click_count || 0,
          views: video.view_count || 0
        };
      });
      setEngagementCounts(prev => ({ ...prev, ...counts }));
      return;
    }
    
    const withIds = (videos || []).filter(v => !!(v.listing_id || v.video_id));
    console.log('Loading like/love statuses for videos:', withIds.map(v => v.listing_id || v.video_id));
    try {
      // No like/love status endpoint for videos; derive initial state from counts
      const statuses = withIds.map(video => ({
        listingId: video.listing_id || video.video_id,
            liked: false,
            loved: false,
            likeCount: video.like_count || 0,
            loveCount: video.love_count || 0,
            clickCount: video.click_count || 0,
            viewCount: video.view_count || 0
      }));
      
      // Update liked/loved items state
      const newLikedItems = {};
      const newLovedItems = {};
      const newEngagementCounts = {};
      
      statuses.forEach(status => {
        newLikedItems[status.listingId] = status.liked;
        newLovedItems[status.listingId] = status.loved;
        newEngagementCounts[status.listingId] = {
          likes: status.likeCount,
          loves: status.loveCount,
          clicks: status.clickCount,
          views: status.viewCount
        };
      });
      
      setLikedItems(prev => ({ ...prev, ...newLikedItems }));
      setLovedItems(prev => ({ ...prev, ...newLovedItems }));
      setEngagementCounts(prev => ({ ...prev, ...newEngagementCounts }));
      
      console.log('Loaded like/love statuses:', statuses);
    } catch (error) {
      console.error('Error loading like/love statuses:', error);
    }
  };

  const handleShare = () => {
    console.log('Share button clicked');
    setShowShareModal(true);
  };

  const handleStore = () => {
    console.log('Store button clicked');
    setShowStoreModal(true);
  };

  const handleProductClick = (product, e) => {
    if (e) {
      e.stopPropagation();
    }
    console.log('Product clicked:', product);
    setSelectedProductData(product);
    setShowProductModal(true);
  };

  const formatCount = (count) => {
    if (count >= 1000000) {
      return (count / 1000000).toFixed(1) + 'M';
    } else if (count >= 1000) {
      return (count / 1000).toFixed(1) + 'k';
    }
    return count.toString();
  };

  const handleSwipeUp = async () => {
    if (!isTransitioning) {
      setIsTransitioning(true);
      
      // If we're at the last video, try to load more
      if (currentVideoIndex >= videos.length - 1) {
        try {
          await loadVideos(videos.length, 5, false);
        } catch (error) {
          console.error('Error loading more videos:', error);
        }
      }
      
      // Move to next video if available
      if (currentVideoIndex < videos.length - 1) {
        setCurrentVideoIndex(currentVideoIndex + 1);
      } else {
        // Show end message
        toast.error('No more videos available');
      }
      
      setTimeout(() => setIsTransitioning(false), 300);
    }
  };

  const handleSwipeDown = () => {
    if (!isTransitioning) {
      setIsTransitioning(true);
      
      if (currentVideoIndex > 0) {
        setCurrentVideoIndex(currentVideoIndex - 1);
      } else {
        // Already at beginning, stay there or show message
        toast.error('You\'re at the beginning of the feed');
      }
      
      setTimeout(() => setIsTransitioning(false), 300);
    }
  };

  const touchStartY = useRef(null);
  const touchStartX = useRef(null);
  const isScrollingCarousel = useRef(false);

  const handleTouchStart = (e) => {
    // Check if touch started on a scrollable element (carousel)
    const target = e.target;
    const scrollableParent = target.closest('.overflow-x-auto');
    if (scrollableParent) {
      isScrollingCarousel.current = true;
      return;
    }
    
    isScrollingCarousel.current = false;
    touchStartY.current = e.targetTouches[0].clientY;
    touchStartX.current = e.targetTouches[0].clientX;
  };

  const handleTouchMove = (e) => {
    // If scrolling carousel, don't interfere
    if (isScrollingCarousel.current) {
      return;
    }
    
    // Check if this is a horizontal scroll (carousel)
    if (touchStartX.current !== null) {
      const deltaX = Math.abs(e.targetTouches[0].clientX - touchStartX.current);
      const deltaY = Math.abs(e.targetTouches[0].clientY - touchStartY.current);
      
      // If horizontal movement is greater, it's a carousel scroll
      if (deltaX > deltaY && deltaX > 10) {
        isScrollingCarousel.current = true;
        return;
      }
    }
  };

  const handleTouchEnd = (e) => {
    // If we were scrolling carousel, reset and don't trigger swipe
    if (isScrollingCarousel.current) {
      isScrollingCarousel.current = false;
      touchStartY.current = null;
      touchStartX.current = null;
      return;
    }
    
    if (touchStartY.current === null) return;
    
    const touchEndY = e.changedTouches[0].clientY;
    const distance = touchStartY.current - touchEndY;
    const isUpSwipe = distance > 50;
    const isDownSwipe = distance < -50;

    if (isUpSwipe) {
      handleSwipeUp();
    } else if (isDownSwipe) {
      handleSwipeDown();
    }
    
    touchStartY.current = null;
    touchStartX.current = null;
  };

  if (loading) {
    return (
      <div className="h-screen bg-white flex items-center justify-center">
        <div className="text-gray-900 text-xl font-poppins">Loading videos...</div>
      </div>
    );
  }

  if (videos.length === 0) {
    return (
      <div className="h-screen bg-white flex items-center justify-center">
        <div className="text-center">
          <div className="text-gray-900 text-xl mb-4 font-poppins">No videos available</div>
          <button 
            onClick={() => loadVideos(0, 5, true)}
            className="bg-[#6A00FF] text-white px-6 py-2 rounded-full font-poppins font-medium"
          >
            Refresh
          </button>
        </div>
      </div>
    );
  }

  return (
      <div 
      className="h-screen bg-white overflow-hidden relative"
        onTouchStart={handleTouchStart}
        onTouchMove={handleTouchMove}
        onTouchEnd={handleTouchEnd}
      ref={videoContainerRef}
    >
      {/* Videos Container with Transform */}
      <div 
        className="relative h-full transition-transform duration-300 ease-out"
        style={{ 
          transform: `translateY(-${currentVideoIndex * 100}vh)`,
          height: `${videos.length * 100}vh`
        }}
      >

        {videos.map((video, index) => {
          const primaryProduct = Array.isArray(video.products) && video.products.length > 0 ? video.products[0] : video;
          const primaryLink = (Array.isArray(video.products) && video.products[0]?.link) || video.link;

          return (
          <div
            key={video.listing_id}
            className="absolute inset-0 w-full h-screen"
            style={{ top: `${index * 100}vh` }}
          >
            {/* Video Background with Blur */}
            {video.thumbnail_url && (
              <div 
                className="absolute inset-0 bg-cover bg-center"
                style={{
                  backgroundImage: `url(${video.thumbnail_url})`,
                  filter: 'blur(20px)',
                  transform: 'scale(1.1)'
                }}
              />
            )}

            {/* Light Overlay */}
            <div className="absolute inset-0 bg-white bg-opacity-20" />

            {/* Main Video Container */}
            <div 
              className="relative h-full flex items-center justify-center"
              onClick={(e) => {
                // Only trigger video click if clicking directly on video area, not buttons or carousels
                const target = e.target;
                const isButton = target.closest('button') || target.closest('[role="button"]');
                const isCarousel = target.closest('.overflow-x-auto');
                const isInteractive = target.closest('[onClick]') && target !== e.currentTarget;
                
                if (!isButton && !isCarousel && !isInteractive) {
                  handleVideoClick();
                }
              }}
            >
              {video.video_url ? (
                <video
                  ref={setVideoRef(index)}
                  className="h-full w-full object-cover"
                  src={video.video_url}
                  muted={isMuted}
                  loop
                  playsInline
                  preload="metadata"
                  onLoadedData={() => {
                    if (index === currentVideoIndex) {
                      const videoEl = videoRefs[index];
                      if (videoEl) {
                        videoEl.play().catch(console.error);
                        setIsPlaying(true);
                      }
                    }
                  }}
                  onError={(e) => {
                    console.error('Video error:', e);
                    console.error('Video src:', video.video_url);
                    toast.error(`Video failed to load: ${video.video_url}`);
                  }}
                  onCanPlay={() => {
                    if (index === currentVideoIndex) {
                      const videoEl = videoRefs[index];
                      if (videoEl) {
                        videoEl.play().catch(console.error);
                        setIsPlaying(true);
                      }
                    }
                  }}
                />
              ) : (
                /* Fallback when no video URL */
                <div className="h-full w-full flex items-center justify-center bg-gradient-to-br from-[#6A00FF] to-purple-200">
                  <div className="text-center">
                    <div className="w-24 h-24 bg-white bg-opacity-30 rounded-full flex items-center justify-center mx-auto mb-4">
                      <Play className="w-12 h-12 text-white" />
                    </div>
                    <p className="text-white text-lg font-medium font-poppins">No video available</p>
                    <p className="text-purple-100 text-sm mt-2 font-poppins">This listing doesn't have a video</p>
                  </div>
                </div>
              )}

              {/* Video Controls Overlay */}
              <div className="absolute inset-0 flex items-center justify-center pointer-events-none">
                {!isPlaying && video.video_url && index === currentVideoIndex && (
                  <div className="bg-white bg-opacity-80 rounded-full p-6 pointer-events-auto">
                    <Play className="w-16 h-16 text-[#6A00FF]" />
                  </div>
                )}
              </div>

              {/* Mute Button - Top Right */}
              {index === currentVideoIndex && (
                <button
                  onClick={(e) => {
                    e.stopPropagation();
                    handleMuteToggle();
                  }}
                  className="absolute top-16 right-4 bg-white bg-opacity-90 rounded-full p-2 backdrop-blur-sm z-30 pointer-events-auto"
                >
                  {isMuted ? (
                    <VolumeX className="w-4 h-4 text-[#6A00FF]" />
                  ) : (
                    <Volume2 className="w-4 h-4 text-[#6A00FF]" />
                  )}
                </button>
              )}
            </div>

            {/* Top Header - Profile Info */}
            {index === currentVideoIndex && (
              <div className="absolute top-4 left-4 right-4 z-30 flex items-center gap-3 pointer-events-auto">
                <button
                  onClick={(e) => {
                    e.stopPropagation();
                    navigate('/');
                  }}
                  className="bg-white bg-opacity-90 rounded-full p-2 backdrop-blur-sm"
                >
                  <ArrowLeft className="w-5 h-5 text-[#6A00FF]" />
                </button>
                <div className="flex items-center gap-2 flex-1 min-w-0">
                  {/* Profile Picture - Clickable */}
                  <div 
                    className="w-10 h-10 bg-[#6A00FF] rounded-full flex items-center justify-center flex-shrink-0 cursor-pointer hover:opacity-80 transition-opacity"
                    onClick={(e) => {
                      e.stopPropagation();
                      if (video.user_id) {
                        navigate(`/profile?user_id=${video.user_id}`);
                      }
                    }}
                  >
                    {video.thumbnail_url ? (
                      <img 
                        src={video.thumbnail_url} 
                        alt={video.provider_name}
                        className="w-full h-full rounded-full object-cover"
                      />
                    ) : (
                      <ShoppingCart className="w-5 h-5 text-white" />
                    )}
                  </div>
                  {/* Name and Brand Tag */}
                  <div className="flex items-center gap-2 min-w-0 flex-1">
                    <span 
                      className="text-white font-semibold text-sm truncate font-poppins cursor-pointer hover:underline"
                      style={{ textShadow: '0 1px 3px rgba(0,0,0,0.5)' }}
                      onClick={(e) => {
                        e.stopPropagation();
                        if (video.user_id) {
                          navigate(`/profile?user_id=${video.user_id}`);
                        }
                      }}
                    >
                      {video.provider_name || video.handle_address || 'User'}
                    </span>
                    <span className="bg-[#6A00FF] text-white text-xs px-2 py-0.5 rounded-full font-medium flex-shrink-0 font-poppins">
                      {video.provider_type === 'INFLUENCER' ? 'Influencer' : video.provider_type === 'ADMIN' ? 'Admin' : 'Brand'}
                    </span>
                    <div className="w-4 h-4 bg-[#6A00FF] rounded-full flex items-center justify-center flex-shrink-0">
                      <Check className="w-3 h-3 text-white" />
                    </div>
                  </div>
                  {/* Follow Button */}
                  {isAuthenticated && video.user_id && user && user.user_id !== video.user_id && (
                    <button
                      onClick={(e) => handleFollowToggle(video.user_id, e)}
                      disabled={loadingFollow[video.user_id]}
                      className={`bg-white bg-opacity-90 rounded-full px-3 py-1.5 backdrop-blur-sm flex items-center gap-1.5 flex-shrink-0 transition-colors ${
                        followingUsers[video.user_id]
                          ? 'hover:bg-gray-200'
                          : 'hover:bg-[#6A00FF] hover:text-white'
                      }`}
                    >
                      {loadingFollow[video.user_id] ? (
                        <span className="text-xs font-semibold text-gray-500 font-poppins">...</span>
                      ) : followingUsers[video.user_id] ? (
                        <>
                          <UserMinus className="w-3.5 h-3.5 text-gray-700" />
                          <span className="text-xs font-semibold text-gray-700 font-poppins">Following</span>
                        </>
                      ) : (
                        <>
                          <UserPlus className="w-3.5 h-3.5 text-[#6A00FF]" />
                          <span className="text-xs font-semibold text-[#6A00FF] font-poppins">Follow</span>
                        </>
                      )}
                    </button>
                  )}
                </div>
              </div>
            )}

            {/* Engagement Bar (Right Side) - Modern Clean Design */}
            {index === currentVideoIndex && (
              <div className="absolute right-3 top-1/2 transform -translate-y-1/2 flex flex-col items-center z-30 pointer-events-auto" style={{ gap: '1px' }}>
                {/* Views */}
                <div className="flex flex-col items-center" style={{ gap: '0px' }}>
                  <div className="p-2">
                    <svg 
                      xmlns="http://www.w3.org/2000/svg" 
                      width="20" 
                      height="20" 
                      viewBox="0 0 512 512"
                      className="text-white fill-white"
                    >
                      <circle cx="256" cy="256" r="64" fill="currentColor"/>
                      <path fill="currentColor" d="M394.82 141.18C351.1 111.2 304.31 96 255.76 96c-43.69 0-86.28 13-126.59 38.48C88.52 160.23 48.67 207 16 256c26.42 44 62.56 89.24 100.2 115.18C159.38 400.92 206.33 416 255.76 416c49 0 95.85-15.07 139.3-44.79C433.31 345 469.71 299.82 496 256c-26.38-43.43-62.9-88.56-101.18-114.82M256 352a96 96 0 1 1 96-96a96.11 96.11 0 0 1-96 96"/>
                    </svg>
                  </div>
                  <span className="text-white text-[10px] font-semibold font-poppins -mt-2" style={{ textShadow: '0 2px 4px rgba(0,0,0,0.8)' }}>
                    {formatCount(engagementCounts[video.video_id || video.listing_id]?.views || 0)}
                  </span>
                </div>

                {/* Likes - Thumbs Up */}
                <button
                  onClick={(e) => {
                    e.stopPropagation();
                    handleLike(video.video_id || video.listing_id);
                  }}
                  className="flex flex-col items-center group cursor-pointer"
                  style={{ gap: '0px' }}
                >
                  <div className="p-2 group-active:scale-95 transition-transform">
                    <svg 
                      xmlns="http://www.w3.org/2000/svg" 
                      width="20" 
                      height="20" 
                      viewBox="0 0 24 24"
                      className={`transition-colors duration-300 ${
                        likedItems[video.video_id || video.listing_id] 
                          ? 'text-blue-400 fill-blue-400' 
                          : 'text-white fill-white'
                      }`}
                    >
                      <path fill="currentColor" d="m20.27 16.265l.705-4.08a1.666 1.666 0 0 0-1.64-1.95h-5.181a.833.833 0 0 1-.822-.969l.663-4.045a4.8 4.8 0 0 0-.09-1.973a1.64 1.64 0 0 0-1.092-1.137l-.145-.047a1.35 1.35 0 0 0-.994.068c-.34.164-.588.463-.68.818l-.476 1.834a7.6 7.6 0 0 1-.656 1.679c-.415.777-1.057 1.4-1.725 1.975l-1.439 1.24a1.67 1.67 0 0 0-.572 1.406l.812 9.393A1.666 1.666 0 0 0 8.597 22h4.648c3.482 0 6.453-2.426 7.025-5.735"/>
                      <path fill="currentColor" fillRule="evenodd" d="M2.968 9.485a.75.75 0 0 1 .78.685l.97 11.236a1.237 1.237 0 1 1-2.468.107V10.234a.75.75 0 0 1 .718-.749" clipRule="evenodd"/>
                    </svg>
                  </div>
                  <span className="text-white text-[10px] font-semibold font-poppins -mt-2" style={{ textShadow: '0 2px 4px rgba(0,0,0,0.8)' }}>
                    {formatCount(engagementCounts[video.video_id || video.listing_id]?.likes || 0)}
                  </span>
                </button>

                {/* Heart/Love */}
                <button
                  onClick={(e) => {
                    e.stopPropagation();
                    handleLove(video.video_id || video.listing_id);
                  }}
                  className="flex flex-col items-center group cursor-pointer"
                  style={{ gap: '0px' }}
                >
                  <div className="p-2 group-active:scale-95 transition-transform">
                    <Heart 
                      className={`w-5 h-5 transition-colors duration-300 ${
                        lovedItems[video.video_id || video.listing_id] 
                          ? 'text-pink-400 fill-pink-400' 
                          : 'text-white fill-white'
                      }`}
                    />
                  </div>
                  <span className="text-white text-[10px] font-semibold font-poppins -mt-2" style={{ textShadow: '0 2px 4px rgba(0,0,0,0.8)' }}>
                    {formatCount(engagementCounts[video.video_id || video.listing_id]?.loves || 0)}
                  </span>
                </button>

                {/* Share */}
                <button
                  onClick={(e) => {
                    e.stopPropagation();
                    handleShare();
                  }}
                  className="flex flex-col items-center group cursor-pointer"
                  style={{ gap: '0px' }}
                >
                  <div className="p-2 group-active:scale-95 transition-transform">
                    <svg 
                      xmlns="http://www.w3.org/2000/svg" 
                      width="20" 
                      height="20" 
                      viewBox="0 0 24 24"
                      className="text-white fill-white"
                    >
                      <path fill="currentColor" fillRule="evenodd" d="M15.75 4.5a3 3 0 1 1 .825 2.066l-8.421 4.679a3 3 0 0 1 0 1.51l8.421 4.679a3 3 0 1 1-.729 1.31l-8.421-4.678a3 3 0 1 1 0-4.132l8.421-4.679a3 3 0 0 1-.096-.755" clipRule="evenodd"/>
                    </svg>
                  </div>
                  <span className="text-white text-[10px] font-semibold font-poppins -mt-2" style={{ textShadow: '0 2px 4px rgba(0,0,0,0.8)' }}>
                    Share
                  </span>
                </button>

                {/* Coupon */}
                <button
                  onClick={(e) => {
                    e.stopPropagation();
                    const currentVideo = videos[currentVideoIndex];
                    const primaryProduct = Array.isArray(currentVideo?.products) && currentVideo.products.length > 0 ? currentVideo.products[0] : currentVideo;
                    openCouponModal(primaryProduct);
                  }}
                  className="flex flex-col items-center group cursor-pointer"
                  style={{ gap: '0px' }}
                >
                  <div className="p-2 group-active:scale-95 transition-transform">
                    <svg 
                      xmlns="http://www.w3.org/2000/svg" 
                      width="20" 
                      height="20" 
                      viewBox="0 0 24 24"
                      className="text-white fill-white"
                    >
                      <path d="m12.593 23.258l-.011.002l-.071.035l-.02.004l-.014-.004l-.071-.035q-.016-.005-.024.005l-.004.01l-.017.428l.005.02l.01.013l.104.074l.015.004l.012-.004l.104-.074l.012-.016l.004-.017l-.017-.427q-.004-.016-.017-.018m.265-.113l-.013.002l-.185.093l-.01.01l-.003.011l.018.43l.005.012l.008.007l.201.093q.019.005.029-.008l.004-.014l-.034-.614q-.005-.018-.02-.022m-.715.002a.02.02 0 0 0-.027.006l-.006.014l-.034.614q.001.018.017.024l.015-.002l.201-.093l.01-.008l.004-.011l.017-.43l-.003-.012l-.01-.01z"/>
                      <path fill="currentColor" d="M19 4a3 3 0 0 1 2.995 2.824L22 7v1.817a1.55 1.55 0 0 1-.776 1.33l-.107.058a2 2 0 0 0-.14 3.515l.14.075c.433.214.82.656.876 1.24l.007.148V17a3 3 0 0 1-2.824 2.995L19 20H5a3 3 0 0 1-2.995-2.824L2 17v-1.817c0-.606.352-1.078.776-1.33l.107-.058a2 2 0 0 0 .14-3.515l-.14-.075c-.433-.214-.82-.656-.876-1.24L2 8.818V7a3 3 0 0 1 2.824-2.995L5 4zm0 2H5a1 1 0 0 0-.993.883L4 7v1.535a4 4 0 0 1 .185 6.816L4 15.465V17a1 1 0 0 0 .883.993L5 18h14a1 1 0 0 0 .993-.883L20 17v-1.535a4 4 0 0 1-.185-6.816L20 8.535V7a1 1 0 0 0-.883-.993zm-9 3a1 1 0 0 1 .993.883L11 10v4a1 1 0 0 1-1.993.117L9 14v-4a1 1 0 0 1 1-1"/>
                    </svg>
                  </div>
                  <span className="text-white text-[10px] font-semibold font-poppins -mt-2" style={{ textShadow: '0 2px 4px rgba(0,0,0,0.8)' }}>
                    Coupon
                  </span>
                </button>
              </div>
            )}

              {/* Bottom Content Area - Product Carousel and Video Carousel */}
              {index === currentVideoIndex && (
                <div className="absolute bottom-2 left-4 right-4 z-30 pointer-events-auto">
                {/* Product Carousel Section */}
                {relatedProducts.length > 0 && (
                  <div className="mb-3 pointer-events-auto">
                    <div 
                      className="flex gap-3 overflow-x-auto scrollbar-hide pb-2 pointer-events-auto"
                      onTouchStart={(e) => e.stopPropagation()}
                      onTouchMove={(e) => e.stopPropagation()}
                      onTouchEnd={(e) => e.stopPropagation()}
                      style={{ 
                        scrollSnapType: 'x mandatory',
                        WebkitOverflowScrolling: 'touch'
                      }}
                    >
                      {/* Current Product (Product 1) */}
                      <div
                        onClick={(e) => {
                          e.stopPropagation();
                          if (primaryLink) {
                            window.open(primaryLink, '_blank', 'noopener,noreferrer');
                          } else {
                            toast.error('Product link not available');
                          }
                        }}
                        className="flex-shrink-0 cursor-pointer transition-all duration-200 pointer-events-auto"
                        style={{ 
                          scrollSnapAlign: 'start',
                          width: 'calc((100vw - 48px) / 3.2)',
                          minWidth: 'calc((100vw - 48px) / 3.2)'
                        }}
                      >
                        <div className="w-full rounded-xl border-3 border-[#6A00FF] shadow-lg shadow-[#6A00FF]/30 overflow-hidden relative" style={{ height: '140px' }}>
                          <div className="w-full h-full relative">
                            {video.thumbnail_url ? (
                              <img 
                                src={video.thumbnail_url} 
                                alt={video.product_name}
                                className="w-full h-full object-cover"
                                onError={(e) => {
                                  e.target.style.display = 'none';
                                  e.target.nextSibling.style.display = 'flex';
                                }}
                              />
                            ) : null}
                            <div 
                              className={`w-full h-full bg-gradient-to-br from-[#6A00FF] to-purple-200 flex items-center justify-center ${video.thumbnail_url ? 'hidden' : 'flex'}`}
                            >
                              <span className="text-white text-xs font-bold text-center px-1 font-poppins">
                                {video.product_name || 'Current'}
                              </span>
                            </div>
                            {/* Product Badge Inside Card */}
                            <div className="absolute bottom-0 left-0 right-0 flex items-center justify-center pb-2">
                              <span className="bg-[#6A00FF] text-white text-xs px-3 py-1 rounded-full font-semibold font-poppins">Product 1</span>
                            </div>
                          </div>
                        </div>
                      </div>

                      {/* Related Products */}
                      {relatedProducts.map((product, productIndex) => {
                        return (
                        <div
                          key={product.listing_id}
                          onClick={(e) => {
                            e.stopPropagation();
                            if (product.link) {
                              window.open(product.link, '_blank', 'noopener,noreferrer');
                            } else {
                              toast.error('Product link not available');
                            }
                          }}
                          className="flex-shrink-0 cursor-pointer transition-all duration-200 pointer-events-auto"
                          style={{ 
                            scrollSnapAlign: 'start',
                            width: 'calc((100vw - 48px) / 3.2)',
                            minWidth: 'calc((100vw - 48px) / 3.2)'
                          }}
                        >
                          <div className="w-full rounded-xl border-3 border-white/60 overflow-hidden shadow-md hover:border-[#6A00FF] hover:border-opacity-80 transition-all relative" style={{ height: '140px' }}>
                            <div className="w-full h-full relative">
                              {product.thumbnail_url ? (
                                <img 
                                  src={product.thumbnail_url} 
                                  alt={product.product_name}
                                  className="w-full h-full object-cover"
                                  onError={(e) => {
                                    e.target.style.display = 'none';
                                    e.target.nextSibling.style.display = 'flex';
                                  }}
                                />
                              ) : null}
                              <div 
                                className={`w-full h-full bg-gradient-to-br from-gray-300 to-gray-400 flex items-center justify-center ${product.thumbnail_url ? 'hidden' : 'flex'}`}
                              >
                                <span className="text-gray-700 text-xs font-bold text-center px-1 font-poppins">
                                  {product.product_name || 'Product'}
                                </span>
                              </div>
                              {/* Product Badge Inside Card */}
                              <div className="absolute bottom-0 left-0 right-0 flex items-center justify-center pb-2">
                                <span className="bg-[#6A00FF] text-white text-xs px-3 py-1 rounded-full font-semibold font-poppins">Product {productIndex + 2}</span>
                              </div>
                            </div>
                          </div>
                        </div>
                      )})}
                    </div>
                  </div>
                )}

              </div>
            )}
          </div>
        )})}
        
        {/* End of Feed Indicator */}
        <div 
          className="absolute inset-0 w-full h-screen flex items-center justify-center bg-gradient-to-br from-gray-200 to-gray-300"
          style={{ top: `${videos.length * 100}vh` }}
        >
          <div className="text-center text-gray-900">
            <div className="text-6xl mb-4"></div>
            <h3 className="text-2xl font-bold mb-2 font-poppins">You've reached the end!</h3>
            <p className="text-gray-600 mb-6 font-poppins">No more videos available</p>
            <button 
              onClick={() => {
                setCurrentVideoIndex(0);
                loadVideos(0, 5, true);
              }}
              className="bg-[#6A00FF] hover:bg-purple-700 text-white px-6 py-3 rounded-full font-semibold transition-colors font-poppins"
            >
              Start Over
            </button>
              </div>
          </div>
      </div>


      {/* Keyboard Support for Desktop */}
      <div 
        className="absolute inset-0 focus:outline-none pointer-events-none" 
        tabIndex={0}
        onKeyDown={(e) => {
          if (e.key === 'ArrowUp') {
            handleSwipeUp();
          } else if (e.key === 'ArrowDown') {
            handleSwipeDown();
          }
        }}
      />

      {/* Product Modal */}
      {showProductModal && selectedProductData && (
        <div className="fixed inset-0 bg-black bg-opacity-60 flex items-center justify-center z-[10001] p-4">
          <div className="bg-white rounded-xl max-w-md w-full max-h-[90vh] overflow-y-auto">
            {/* Header */}
            <div className="flex items-center justify-between p-6 border-b border-gray-200">
              <h2 className="text-xl font-bold text-gray-900">
                Product Details
              </h2>
              <button
                onClick={() => setShowProductModal(false)}
                className="text-gray-400 hover:text-gray-600"
              >
                <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                </svg>
              </button>
            </div>

            {/* Content */}
            <div className="p-6">
              {/* Product Image */}
              {selectedProductData.thumbnail_url && (
                <div className="mb-4">
                  <img 
                    src={selectedProductData.thumbnail_url} 
                    alt={selectedProductData.product_name}
                    className="w-full h-48 object-cover rounded-lg"
                  />
                  </div>
              )}

              {/* Product Info */}
              <div className="space-y-4">
                <div>
                  <h3 className="text-lg font-semibold text-gray-900">
                    {selectedProductData.product_name}
                  </h3>
                  <p className="text-gray-600">
                    by {selectedProductData.provider_name || selectedProductData.handle_address}
                  </p>
                </div>

                {selectedProductData.price && (
                  <div className="text-2xl font-bold text-gray-900">
                      {selectedProductData.price}
                  </div>
                )}

                {/* Description */}
                {selectedProductData.description && (
                  <div>
                    <h5 className="font-semibold text-gray-900 mb-2">Description</h5>
                    <p className="text-gray-600 text-sm leading-relaxed">
                      {selectedProductData.description}
                    </p>
                  </div>
                )}

                {/* Engagement Stats */}
                <div className="grid grid-cols-2 gap-4 pt-4 border-t border-gray-200">
                  <div className="text-center">
                    <div className="text-2xl font-bold text-gray-900">
                      {formatCount(selectedProductData.view_count || 0)}
                    </div>
                    <div className="text-xs text-gray-500 uppercase tracking-wide">Views</div>
                  </div>
                  <div className="text-center">
                    <div className="text-2xl font-bold text-gray-900">
                      {formatCount(selectedProductData.like_count || 0)}
                    </div>
                    <div className="text-xs text-gray-500 uppercase tracking-wide">Likes</div>
                  </div>
                </div>
              </div>
            </div>

            {/* Footer */}
            <div className="p-6 border-t border-gray-200 bg-gray-50">
              {selectedProductData.link ? (
                <a
                  href={selectedProductData.link}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="w-full bg-orange-500 text-white py-3 px-4 rounded-xl font-bold text-center block hover:bg-orange-600 transition-colors shadow-lg"
                >
                  View Product
                </a>
              ) : (
                <div className="w-full bg-gray-300 text-gray-500 py-3 px-4 rounded-xl font-semibold text-center">
                  No link available
                </div>
              )}
            </div>
          </div>
        </div>
      )}

      {couponModalProduct && (
        <div
          className="fixed inset-0 bg-black bg-opacity-60 flex items-end justify-center z-[10001] animate-fadeIn"
          onClick={closeCouponModal}
        >
          <div
            className="bg-white w-full max-w-sm rounded-t-2xl shadow-2xl animate-slideUp overflow-hidden"
            onClick={(e) => e.stopPropagation()}
          >
            <div className="flex items-center justify-between px-5 py-4 border-b border-gray-100">
              <div>
                <p className="text-xs uppercase tracking-wide text-gray-400 font-semibold">Coupons</p>
                <p className="text-sm font-semibold text-gray-900 mt-0.5">
                  {couponModalProduct.product_name || couponModalProduct.name || 'Product'}
                </p>
              </div>
              <button
                onClick={closeCouponModal}
                className="text-gray-400 hover:text-gray-600 transition-colors"
                aria-label="Close coupon modal"
              >
                <X className="w-5 h-5" />
              </button>
            </div>

            <div className="p-5 space-y-2 max-h-[60vh] overflow-y-auto">
              {couponModalProduct.coupons?.length ? (
                couponModalProduct.coupons.map((coupon, idx) => (
                  <button
                    key={`${coupon.code}-${idx}`}
                    onClick={() => handleCopyCoupon(coupon.code)}
                    className="w-full text-left border border-gray-200 rounded-lg px-3 py-2 hover:border-gray-300 transition-colors flex items-center justify-between gap-3"
                  >
                    <div>
                      <p className="font-mono text-sm font-semibold text-gray-900 tracking-wide">{coupon.code}</p>
                      {coupon.description && (
                        <p className="text-xs text-gray-500 mt-1">{coupon.description}</p>
                      )}
                    </div>
                    <Copy className="w-4 h-4 text-gray-400" />
                  </button>
                ))
              ) : (
                <div className="text-center py-6 text-sm text-gray-500">
                  No coupons available for this product yet
                </div>
              )}
            </div>
            <div className="px-5 py-3 border-t border-gray-100 text-center text-xs text-gray-500">
              Tap any coupon to copy it
            </div>
          </div>
        </div>
      )}

      {/* Share Modal */}
      {showShareModal && (
        <div className="fixed inset-0 bg-black bg-opacity-60 flex items-end justify-center z-[10001] animate-fadeIn">
          <div 
            className="bg-white w-full max-w-sm rounded-t-3xl shadow-2xl animate-slideUp overflow-hidden"
            onClick={(e) => e.stopPropagation()}
          >
            {/* Handle bar */}
            <div className="flex justify-center pt-3 pb-2">
              <div className="w-12 h-1 bg-gray-600 rounded-full"></div>
            </div>

            {/* Header */}
            <div className="px-6 py-4 border-b border-gray-200 flex items-center justify-between">
              <h3 className="text-gray-900 text-xl font-bold font-poppins">Share</h3>
              <button
                onClick={() => setShowShareModal(false)}
                className="text-gray-400 hover:text-gray-600 transition-colors"
                aria-label="Close share modal"
              >
                <X className="w-6 h-6" />
              </button>
            </div>

            {/* Share Options */}
            <div className="p-6 space-y-4">
              {/* Copy Link */}
              <button
                onClick={async () => {
                  try {
                    const currentVideo = videos[currentVideoIndex];
                    const shareUrl = `${window.location.origin}/watch?video=${currentVideo?.listing_id}`;
                    await navigator.clipboard.writeText(shareUrl);
                    toast.success('Link copied to clipboard!');
                    setShowShareModal(false);
                  } catch (error) {
                    toast.error('Failed to copy link');
                  }
                }}
                className="w-full bg-[#6A00FF] hover:bg-purple-700 text-white py-4 px-6 rounded-xl font-semibold transition-colors flex items-center justify-center space-x-3 font-poppins"
              >
                <Share2 className="w-5 h-5" />
                <span>Copy Link</span>
              </button>

              {/* Product Info */}
              <div className="bg-gray-100 rounded-xl p-4 mt-4">
                <div className="flex items-center space-x-3">
                  {videos[currentVideoIndex]?.thumbnail_url && (
                    <img 
                      src={videos[currentVideoIndex].thumbnail_url} 
                      alt={videos[currentVideoIndex].product_name}
                      className="w-12 h-12 rounded-lg object-cover"
                    />
                  )}
                  <div className="flex-1">
                    <p className="text-gray-900 font-semibold text-sm font-poppins">
                      {videos[currentVideoIndex]?.product_name}
                    </p>
                    <p className="text-gray-600 text-xs font-poppins">
                      by @{videos[currentVideoIndex]?.handle_address}
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Store Modal */}
      {showStoreModal && (
        <div className="fixed inset-0 bg-black bg-opacity-60 flex items-end justify-center z-[10001] animate-fadeIn">
          <div 
            className="bg-white w-full max-w-sm rounded-t-3xl shadow-2xl animate-slideUp overflow-hidden max-h-[80vh]"
            onClick={(e) => e.stopPropagation()}
          >
            {/* Handle bar for dragging */}
            <div 
              className="flex justify-center pt-3 pb-2 cursor-grab active:cursor-grabbing"
              onMouseDown={(e) => {
                const modal = e.currentTarget.closest('.bg-black');
                let startY = e.clientY;
                let startHeight = modal.offsetHeight;
                
                const handleMouseMove = (e) => {
                  const deltaY = startY - e.clientY;
                  const newHeight = startHeight + deltaY;
                  const maxHeight = window.innerHeight * 0.8;
                  const minHeight = 200;
                  
                  if (newHeight >= minHeight && newHeight <= maxHeight) {
                    modal.style.height = `${newHeight}px`;
                  }
                };
                
                const handleMouseUp = () => {
                  const modal = e.currentTarget.closest('.bg-black');
                  const currentHeight = modal.offsetHeight;
                  const threshold = window.innerHeight * 0.3;
                  
                  if (currentHeight < threshold) {
                    setShowStoreModal(false);
                  } else {
                    modal.style.height = '80vh';
                  }
                  
                  document.removeEventListener('mousemove', handleMouseMove);
                  document.removeEventListener('mouseup', handleMouseUp);
                };
                
                document.addEventListener('mousemove', handleMouseMove);
                document.addEventListener('mouseup', handleMouseUp);
              }}
            >
              <div className="w-12 h-1 bg-gray-600 rounded-full"></div>
            </div>

            {/* Header */}
            <div className="px-6 py-4 border-b border-gray-200">
              <div className="flex items-center justify-between">
                <h3 className="text-gray-900 text-xl font-bold font-poppins">Store</h3>
                <button
                  onClick={() => setShowStoreModal(false)}
                  className="text-gray-400 hover:text-gray-600 transition-colors"
                >
                  <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                  </svg>
                </button>
              </div>
            </div>

            {/* Store Content */}
            <div className="p-6 space-y-4 overflow-y-auto max-h-[60vh]">
              {/* Current Product */}
              {videos[currentVideoIndex] && (
                <div className="bg-gray-100 rounded-xl p-4">
                  <div className="flex items-center space-x-3">
                    {videos[currentVideoIndex].thumbnail_url && (
                      <img 
                        src={videos[currentVideoIndex].thumbnail_url} 
                        alt={videos[currentVideoIndex].product_name}
                        className="w-16 h-16 rounded-lg object-cover"
                      />
                    )}
                    <div className="flex-1">
                      <h4 className="text-gray-900 font-semibold font-poppins">
                        {videos[currentVideoIndex].product_name}
                      </h4>
                      <p className="text-gray-600 text-sm font-poppins">
                        by @{videos[currentVideoIndex].handle_address}
                      </p>
                      <p className="text-[#6A00FF] font-semibold mt-1 font-poppins">
                        Featured Product
                      </p>
                    </div>
                  </div>
                </div>
              )}

              {/* Related Products */}
              <div>
                <h4 className="text-gray-900 font-semibold mb-3 font-poppins">More from this Store</h4>
                <div className="space-y-3">
                  {relatedProducts.length > 0 ? (
                    relatedProducts.slice(0, 5).map((product) => (
                      <div key={product.listing_id} className="bg-gray-100 rounded-xl p-4">
                        <div className="flex items-center space-x-3">
                          {product.thumbnail_url && (
                            <img 
                              src={product.thumbnail_url} 
                              alt={product.product_name}
                              className="w-12 h-12 rounded-lg object-cover"
                            />
                          )}
                          <div className="flex-1">
                            <h5 className="text-gray-900 font-medium text-sm font-poppins">
                              {product.product_name}
                            </h5>
                            <p className="text-gray-600 text-xs font-poppins">
                              {product.category_name || 'Product'}
                            </p>
                          </div>
                          <button className="bg-[#6A00FF] hover:bg-purple-700 text-white px-3 py-1 rounded-lg text-xs font-medium transition-colors font-poppins">
                            View
                          </button>
                        </div>
                      </div>
                    ))
                  ) : (
                    <div className="text-center py-8">
                      <p className="text-gray-600 font-poppins">No other products available</p>
                    </div>
                  )}
                </div>
              </div>

              {/* Store Info */}
              <div className="bg-gray-100 rounded-xl p-4">
                <h4 className="text-gray-900 font-semibold mb-2 font-poppins">Store Information</h4>
                <div className="space-y-2 text-sm">
                  <div className="flex justify-between">
                    <span className="text-gray-600 font-poppins">Provider:</span>
                    <span className="text-gray-900 font-poppins">@{videos[currentVideoIndex]?.handle_address}</span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-gray-600 font-poppins">Type:</span>
                    <span className="text-gray-900 font-poppins">{videos[currentVideoIndex]?.provider_type}</span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-gray-600 font-poppins">Products:</span>
                    <span className="text-gray-900 font-poppins">{relatedProducts.length + 1}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default Watch;