import React, { useState, useEffect } from 'react';
import { ArrowLeft, Save, Loader } from 'lucide-react';
import { useNavigate } from 'react-router-dom';
import { userAPI, providerAPI } from '../utils/api';
import { toast } from 'react-hot-toast';
import { useAuth } from '../context/AuthContext';

const EditProfile = () => {
  const navigate = useNavigate();
  const { user: currentUser } = useAuth();
  const [loading, setLoading] = useState(false);
  const [saving, setSaving] = useState(false);
  const [activeTab, setActiveTab] = useState('user');
  const [user, setUser] = useState(null);
  const [provider, setProvider] = useState(null);
  const [formData, setFormData] = useState({
    // User fields
    name: '',
    email: '',
    phone: '',
    profile_picture: '',
    banner: '',
    
    // Provider fields
    name_provider: '',
    phone_provider: '',
    business_name: '',
    type: 'brand',
    location: '',
    gst_number: '',
    business_address: '',
    handle_address: ''
  });

  useEffect(() => {
    loadProfileData();
  }, []);

  const loadProfileData = async () => {
    try {
      setLoading(true);
      const [userData, providerData] = await Promise.all([
        userAPI.getProfile().catch(() => null),
        providerAPI.getMe().catch(() => null)
      ]);

      const userInfo = userData?.user || userData || null;
      const providerInfo = providerData?.provider || providerData || null;

      setUser(userInfo);
      setProvider(providerInfo);

      if (userInfo) {
        setFormData(prev => ({
          ...prev,
          name: userInfo.full_name || userInfo.name || '',
          email: userInfo.email || '',
          phone: userInfo.phone || '',
          profile_picture: userInfo.profile_picture || userInfo.profile_image_url || '',
          banner: userInfo.banner || ''
        }));
      }

      if (providerInfo) {
        setFormData(prev => ({
          ...prev,
          name_provider: providerInfo.name || '',
          phone_provider: providerInfo.phone || '',
          business_name: providerInfo.business_name || '',
          type: (providerInfo.provider_type || providerInfo.type || 'BRAND').toString().toLowerCase(),
          location: providerInfo.location || '',
          gst_number: providerInfo.gst || providerInfo.gst_number || '',
          business_address: providerInfo.business_address || '',
          handle_address: providerInfo.handle_address || ''
        }));
      }
    } catch (error) {
      console.error('Error loading profile:', error);
      toast.error('Failed to load profile data');
    } finally {
      setLoading(false);
    }
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setSaving(true);

    try {
      if (activeTab === 'user') {
        const payload = {
          name: formData.name,
          email: formData.email,
          phone: formData.phone,
          profile_picture: formData.profile_picture === '' ? null : formData.profile_picture,
          banner: formData.banner === '' ? null : formData.banner
        };
        await userAPI.updateProfile(payload);
        toast.success('Profile updated successfully!');
      } else if (activeTab === 'provider' && provider) {
        await providerAPI.updateProfile({
          name: formData.name_provider,
          phone: formData.phone_provider,
          business_name: formData.business_name,
          type: formData.type,
          location: formData.location,
          gst_number: formData.gst_number,
          business_address: formData.business_address,
          handle_address: formData.handle_address
        });
        toast.success('Provider profile updated successfully!');
      }
      
      // Redirect to profile after save
      navigate('/profile');
    } catch (error) {
      console.error('Error updating profile:', error);
      toast.error(error.response?.data?.error || 'Failed to update profile');
    } finally {
      setSaving(false);
    }
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-white flex items-center justify-center">
        <Loader className="w-8 h-8 animate-spin text-[#6A00FF]" />
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-white">
      {/* Header */}
      <div className="bg-white border-b border-gray-200 px-4 sm:px-6 py-4 sticky top-0 z-20">
        <div className="flex items-center gap-4 max-w-4xl mx-auto">
          <button
            onClick={() => navigate('/profile')}
            className="p-2 hover:bg-gray-100 rounded-full transition-colors"
          >
            <ArrowLeft className="w-5 h-5 text-gray-900" />
          </button>
          <h1 className="text-xl font-bold text-gray-900 font-poppins">Edit Profile</h1>
        </div>
      </div>

      <div className="max-w-4xl mx-auto px-4 sm:px-6 py-6">
        {/* Tabs */}
        <div className="flex gap-2 mb-6 border-b border-gray-200">
          <button
            onClick={() => setActiveTab('user')}
            className={`px-4 py-2 font-poppins text-sm font-medium transition-colors ${
              activeTab === 'user'
                ? 'text-[#6A00FF] border-b-2 border-[#6A00FF]'
                : 'text-gray-600 hover:text-gray-900'
            }`}
          >
            User Profile
          </button>
          {provider && (
            <button
              onClick={() => setActiveTab('provider')}
              className={`px-4 py-2 font-poppins text-sm font-medium transition-colors ${
                activeTab === 'provider'
                  ? 'text-[#6A00FF] border-b-2 border-[#6A00FF]'
                  : 'text-gray-600 hover:text-gray-900'
              }`}
            >
              Business Profile
            </button>
          )}
        </div>

        {/* Form */}
        <form onSubmit={handleSubmit} className="space-y-6">
          {activeTab === 'user' ? (
            <div className="space-y-4">
              {/* Banner */}
              <div>
                <label className="block text-gray-700 text-sm font-medium mb-2 font-poppins">
                  Profile Banner URL
                </label>
                <input
                  type="url"
                  name="banner"
                  value={formData.banner}
                  onChange={handleInputChange}
                  placeholder="https://example.com/banner.jpg"
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#6A00FF] focus:border-transparent font-poppins"
                />
                {formData.banner && (
                  <div className="mt-2 w-full h-32 rounded-lg overflow-hidden border border-gray-200">
                    <img src={formData.banner} alt="Banner preview" className="w-full h-full object-cover" />
                  </div>
                )}
              </div>

              {/* Profile Picture */}
              <div>
                <label className="block text-gray-700 text-sm font-medium mb-2 font-poppins">
                  Profile Picture URL
                </label>
                <input
                  type="url"
                  name="profile_picture"
                  value={formData.profile_picture}
                  onChange={handleInputChange}
                  placeholder="https://example.com/profile.jpg"
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#6A00FF] focus:border-transparent font-poppins"
                />
                {formData.profile_picture && (
                  <div className="mt-2 w-20 h-20 rounded-full overflow-hidden border border-gray-200">
                    <img src={formData.profile_picture} alt="Profile preview" className="w-full h-full object-cover" />
                  </div>
                )}
              </div>

              {/* Full Name */}
              <div>
                <label className="block text-gray-700 text-sm font-medium mb-2 font-poppins">
                  Full Name
                </label>
                <input
                  type="text"
                  name="name"
                  value={formData.name}
                  onChange={handleInputChange}
                  required
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#6A00FF] focus:border-transparent font-poppins"
                />
              </div>

              {/* Email */}
              <div>
                <label className="block text-gray-700 text-sm font-medium mb-2 font-poppins">
                  Email
                </label>
                <input
                  type="email"
                  name="email"
                  value={formData.email}
                  onChange={handleInputChange}
                  required
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#6A00FF] focus:border-transparent font-poppins"
                />
              </div>

              {/* Phone */}
              <div>
                <label className="block text-gray-700 text-sm font-medium mb-2 font-poppins">
                  Phone
                </label>
                <input
                  type="tel"
                  name="phone"
                  value={formData.phone}
                  onChange={handleInputChange}
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#6A00FF] focus:border-transparent font-poppins"
                />
              </div>
            </div>
          ) : (
            <div className="space-y-4">
              {/* Business Name */}
              <div>
                <label className="block text-gray-700 text-sm font-medium mb-2 font-poppins">
                  Business Name
                </label>
                <input
                  type="text"
                  name="business_name"
                  value={formData.business_name}
                  onChange={handleInputChange}
                  required
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#6A00FF] focus:border-transparent font-poppins"
                />
              </div>

              {/* Provider Type */}
              <div>
                <label className="block text-gray-700 text-sm font-medium mb-2 font-poppins">
                  Business Type
                </label>
                <select
                  name="type"
                  value={formData.type}
                  onChange={handleInputChange}
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#6A00FF] focus:border-transparent font-poppins"
                >
                  <option value="brand">Brand</option>
                  <option value="influencer">Influencer</option>
                </select>
              </div>

              {/* Location */}
              <div>
                <label className="block text-gray-700 text-sm font-medium mb-2 font-poppins">
                  Location
                </label>
                <input
                  type="text"
                  name="location"
                  value={formData.location}
                  onChange={handleInputChange}
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#6A00FF] focus:border-transparent font-poppins"
                />
              </div>

              {/* Handle/Website */}
              <div>
                <label className="block text-gray-700 text-sm font-medium mb-2 font-poppins">
                  Handle/Website
                </label>
                <input
                  type="url"
                  name="handle_address"
                  value={formData.handle_address}
                  onChange={handleInputChange}
                  placeholder="https://example.com or @handle"
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#6A00FF] focus:border-transparent font-poppins"
                />
              </div>

              {/* GST Number */}
              <div>
                <label className="block text-gray-700 text-sm font-medium mb-2 font-poppins">
                  GST Number (Optional)
                </label>
                <input
                  type="text"
                  name="gst_number"
                  value={formData.gst_number}
                  onChange={handleInputChange}
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#6A00FF] focus:border-transparent font-poppins"
                />
              </div>

              {/* Business Address */}
              <div>
                <label className="block text-gray-700 text-sm font-medium mb-2 font-poppins">
                  Business Address (Optional)
                </label>
                <textarea
                  name="business_address"
                  value={formData.business_address}
                  onChange={handleInputChange}
                  rows={3}
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#6A00FF] focus:border-transparent font-poppins"
                />
              </div>
            </div>
          )}

          {/* Save Button */}
          <div className="flex gap-3 pt-4">
            <button
              type="button"
              onClick={() => navigate('/profile')}
              className="px-6 py-2 border border-gray-300 rounded-lg hover:bg-gray-50 transition-colors font-poppins text-sm"
            >
              Cancel
            </button>
            <button
              type="submit"
              disabled={saving}
              className="flex-1 flex items-center justify-center gap-2 bg-[#6A00FF] hover:bg-purple-700 text-white px-6 py-2 rounded-lg transition-colors font-poppins text-sm disabled:opacity-50 disabled:cursor-not-allowed"
            >
              {saving ? (
                <>
                  <Loader className="w-4 h-4 animate-spin" />
                  Saving...
                </>
              ) : (
                <>
                  <Save className="w-4 h-4" />
                  Save Changes
                </>
              )}
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default EditProfile;


