import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { Clock, CheckCircle, XCircle, RefreshCw, ArrowLeft, Loader } from 'lucide-react';
import { providerAPI } from '../utils/api';
import { toast } from 'react-hot-toast';

const statusVisuals = {
  APPROVED: {
    heading: 'Youre all set to go live!',
    message: 'Your provider profile is ready. Head over to Listings and start showcasing your products.',
    accent: '#22c55e',
    badgeGradient: 'from-emerald-500/20 to-emerald-400/10',
    iconColor: 'text-emerald-400'
  },
  REJECTED: {
    heading: 'We need a quick follow-up',
    message: 'Your submission needs a bit more info. Revisit your details or message our support team to continue.',
    accent: '#f87171',
    badgeGradient: 'from-red-500/20 to-red-400/5',
    iconColor: 'text-red-400'
  },
  PENDING: {
    heading: 'Were reviewing your brand',
    message: 'Thanks for submitting your provider request. Our team is carefully reviewing everythingkeep an eye out for updates.',
    accent: '#c084fc',
    badgeGradient: 'from-purple-500/20 to-indigo-400/5',
    iconColor: 'text-purple-300'
  }
};

const ProviderRequestWaiting = () => {
  const navigate = useNavigate();
  const [requestStatus, setRequestStatus] = useState(null);
  const [loading, setLoading] = useState(true);
  const [autoRefresh, setAutoRefresh] = useState(true);

  useEffect(() => {
    checkStatus();
    const interval = setInterval(() => {
      if (autoRefresh) {
        checkStatus();
      }
    }, 5000);

    return () => clearInterval(interval);
  }, [autoRefresh]);

  const checkStatus = async () => {
    try {
      const response = await providerAPI.getRequestStatus();
      if (response.ok) {
        if (response.has_provider) {
          navigate('/listing');
          return;
        }
        setRequestStatus(response.request_status);
        if (response.request_status === 'APPROVED') {
          setAutoRefresh(false);
          toast.success('Your provider request has been approved!');
          setTimeout(() => navigate('/listing'), 2000);
        }
      }
    } catch (error) {
      console.error('Error checking request status:', error);
    } finally {
      setLoading(false);
    }
  };

  const getStatusIcon = (size = 64) => {
    const style = { width: size, height: size };
    switch (requestStatus) {
      case 'APPROVED':
        return <CheckCircle className="text-emerald-400" style={style} />;
      case 'REJECTED':
        return <XCircle className="text-red-400" style={style} />;
      default:
        return <Clock className="text-yellow-300 animate-pulse" style={style} />;
    }
  };
  const handleSupport = () => {
    window.open('mailto:support@getnayi.com', '_blank');
  };

  const statusInfo = statusVisuals[requestStatus] || statusVisuals.PENDING;

  const timelineSteps = [
    { label: 'Request submitted', state: 'done' },
    { label: 'Under review', state: requestStatus === 'REJECTED' ? 'error' : requestStatus === 'APPROVED' ? 'done' : 'active' },
    { 
      label: requestStatus === 'REJECTED' ? 'Needs updates' : 'Ready to go', 
      state: requestStatus === 'APPROVED' ? 'done' : requestStatus === 'REJECTED' ? 'error' : 'pending' 
    }
  ];

  if (loading) {
    return (
      <div className="min-h-screen bg-gradient-to-b from-[#060014] via-[#0b0424] to-[#140634] flex items-center justify-center">
        <Loader className="w-10 h-10 animate-spin text-[#c084fc]" />
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-b from-[#050015] via-[#0c0324] to-[#12052d] text-white" style={{ fontFamily: 'Inter, sans-serif' }}>
      <div className="max-w-5xl mx-auto px-4 sm:px-6 lg:px-10 py-10">
        <div className="flex items-center justify-between mb-10">
          <button
            onClick={() => navigate('/')}
            className="inline-flex items-center gap-2 text-white/70 hover:text-white transition-colors text-sm tracking-wide"
            style={{ fontFamily: 'Inter, sans-serif' }}
          >
            <ArrowLeft className="w-4 h-4" />
            Back to home
          </button>
          <span className="text-xs uppercase tracking-[0.35em] text-white/50">Provider onboarding</span>
        </div>

        <div className="bg-white/5 border border-white/10 rounded-3xl p-8 sm:p-10 backdrop-blur-xl shadow-[0_25px_80px_rgba(10,0,35,0.35)]">
          <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between gap-8">
            <div>
              <p className="text-xs uppercase tracking-[0.4em] text-white/60 mb-3" style={{ fontFamily: 'Inter, sans-serif' }}>
                Current status
              </p>
              <h1
                className="text-3xl sm:text-4xl font-bold text-white mb-4 leading-tight"
                style={{ fontFamily: 'Gilroy, Inter, sans-serif' }}
              >
                {statusInfo.heading}
              </h1>
              <p className="text-base sm:text-lg text-white/80 leading-relaxed max-w-2xl">
                {statusInfo.message}
              </p>
            </div>
            <div className={`w-28 h-28 rounded-3xl border border-white/15 flex items-center justify-center bg-gradient-to-br ${statusInfo.badgeGradient}`}>
              {getStatusIcon(56)}
            </div>
          </div>

          <div className="mt-10">
            <div className="grid gap-4 sm:grid-cols-3">
              {timelineSteps.map((step, index) => {
                const isActive = step.state === 'active';
                const isDone = step.state === 'done';
                const isError = step.state === 'error';

                return (
                  <div
                    key={step.label}
                    className={`rounded-2xl border px-4 py-5 transition-all duration-200 ${
                      isDone
                        ? 'border-emerald-400/50 bg-emerald-400/10'
                        : isError
                        ? 'border-red-400/50 bg-red-500/10'
                        : isActive
                        ? 'border-purple-400/60 bg-purple-500/10 animate-pulse'
                        : 'border-white/10 bg-white/5'
                    }`}
                  >
                    <div className="flex items-center justify-between mb-2">
                      <span className="text-xs uppercase tracking-[0.3em] text-white/50">
                        {index + 1 < 10 ? `0${index + 1}` : index + 1}
                      </span>
                      <div
                        className={`w-2.5 h-2.5 rounded-full ${
                          isDone
                            ? 'bg-emerald-400'
                            : isError
                            ? 'bg-red-400'
                            : isActive
                            ? 'bg-purple-400'
                            : 'bg-white/30'
                        }`}
                      />
                    </div>
                    <p
                      className="text-sm font-semibold"
                      style={{ fontFamily: 'Gilroy, Inter, sans-serif' }}
                    >
                      {step.label}
                    </p>
                  </div>
                );
              })}
            </div>
          </div>

          {requestStatus === 'PENDING' && (
            <div className="mt-10 grid gap-5 lg:grid-cols-3">
              <div className="lg:col-span-2 border border-white/10 rounded-2xl p-5 bg-white/3">
                <div className="flex items-center justify-between mb-3">
                  <div>
                    <p className="text-xs uppercase tracking-[0.3em] text-white/50">Live updates</p>
                    <p className="text-base text-white font-semibold mt-1">Status monitor</p>
                  </div>
                  <RefreshCw className={`w-5 h-5 ${autoRefresh ? 'animate-spin text-purple-300' : 'text-white/50'}`} />
                </div>
                <p className="text-sm text-white/70">
                  {autoRefresh
                    ? 'Well refresh this status every few seconds so you dont have to.'
                    : 'Auto-refresh is paused. Tap below if you want us to keep checking for you.'}
                </p>
                <div className="mt-4 flex flex-wrap gap-3">
                  <button
                    onClick={checkStatus}
                    className="px-5 py-3 rounded-xl bg-white text-[#12052d] font-semibold hover:bg-white/90 transition-all"
                  >
                    Refresh now
                  </button>
                  <button
                    onClick={() => setAutoRefresh(!autoRefresh)}
                    className="px-5 py-3 rounded-xl border border-white/20 text-white/80 hover:bg-white/10 transition-all"
                  >
                    {autoRefresh ? 'Pause auto-refresh' : 'Resume auto-refresh'}
                  </button>
                </div>
              </div>
              <div className="border border-white/10 rounded-2xl p-5 bg-white/3">
                <p className="text-xs uppercase tracking-[0.3em] text-white/50 mb-2">Need help?</p>
                <p className="text-sm text-white/80 mb-4">
                  Our partner team can answer any question about your onboarding.
                </p>
                  <button
                    onClick={handleSupport}
                    className="w-full px-4 py-2.5 rounded-xl border border-white/20 text-white hover:bg-white/10 transition-all"
                  >
                    Message support
                  </button>
              </div>
            </div>
          )}

          {requestStatus === 'REJECTED' && (
            <div className="mt-10 flex flex-col sm:flex-row gap-4">
              <button
                onClick={() => navigate('/provider-registration')}
                className="flex-1 px-6 py-3 rounded-2xl bg-white text-[#12052d] font-semibold hover:bg-white/90 transition-all"
              >
                Submit new request
              </button>
              <button
                onClick={() => navigate('/')}
                className="flex-1 px-6 py-3 rounded-2xl border border-white/20 text-white hover:bg-white/10 transition-all"
              >
                Return home
              </button>
            </div>
          )}

          {requestStatus === 'APPROVED' && (
            <div className="mt-10 flex flex-col sm:flex-row gap-4">
              <button
                onClick={() => navigate('/listing')}
                className="flex-1 px-6 py-3 rounded-2xl bg-white text-[#12052d] font-semibold hover:bg-white/90 transition-all"
              >
                Go to listings
              </button>
              <button
                onClick={() => navigate('/')}
                className="flex-1 px-6 py-3 rounded-2xl border border-white/20 text-white hover:bg-white/10 transition-all"
              >
                Explore home
              </button>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default ProviderRequestWaiting;
