import React, { useState, useRef, useEffect } from 'react';
import { Settings, Edit3, BarChart3, Share2, LogOut, ChevronDown } from 'lucide-react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import { toast } from 'react-hot-toast';

const SettingsDropdown = ({ user }) => {
  const [isOpen, setIsOpen] = useState(false);
  const dropdownRef = useRef(null);
  const navigate = useNavigate();
  const { logout } = useAuth();

  // Close dropdown when clicking outside
  useEffect(() => {
    const handleClickOutside = (event) => {
      if (dropdownRef.current && !dropdownRef.current.contains(event.target)) {
        setIsOpen(false);
      }
    };

    if (isOpen) {
      document.addEventListener('mousedown', handleClickOutside);
    }

    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [isOpen]);

  const handleShareProfile = () => {
    const profileUrl = user?.username 
      ? `${window.location.origin}/profile/${user.username}`
      : `${window.location.origin}/profile?user_id=${user?.user_id}`;
    
    navigator.clipboard.writeText(profileUrl).then(() => {
      toast.success('Profile link copied to clipboard!');
    }).catch(() => {
      toast.error('Failed to copy link');
    });
    setIsOpen(false);
  };

  const handleLogout = async () => {
    try {
      await logout();
      navigate('/');
      toast.success('Logged out successfully');
    } catch (e) {
      toast.error('Failed to logout');
    }
    setIsOpen(false);
  };

  return (
    <div className="relative" ref={dropdownRef}>
      <button
        onClick={() => setIsOpen(!isOpen)}
        className="flex items-center gap-2 bg-white hover:bg-gray-50 border border-gray-300 text-gray-900 px-4 py-2 rounded-lg transition-colors font-poppins text-sm"
      >
        <Settings className="w-4 h-4" />
        <span className="hidden sm:inline">Settings</span>
        <ChevronDown className={`w-4 h-4 transition-transform ${isOpen ? 'rotate-180' : ''}`} />
      </button>

      {isOpen && (
        <div className="absolute right-0 mt-2 w-56 bg-white rounded-lg shadow-lg border border-gray-200 py-2 z-50">
          <button
            onClick={() => {
              navigate('/settings');
              setIsOpen(false);
            }}
            className="w-full flex items-center gap-3 px-4 py-2.5 text-left hover:bg-gray-50 transition-colors font-poppins text-sm text-gray-900"
          >
            <Settings className="w-4 h-4 text-gray-600" />
            <span>Subscription Details</span>
          </button>

          <button
            onClick={() => {
              navigate('/profile/edit');
              setIsOpen(false);
            }}
            className="w-full flex items-center gap-3 px-4 py-2.5 text-left hover:bg-gray-50 transition-colors font-poppins text-sm text-gray-900"
          >
            <Edit3 className="w-4 h-4 text-gray-600" />
            <span>Edit Profile</span>
          </button>

          <button
            onClick={() => {
              navigate('/profile/analytics');
              setIsOpen(false);
            }}
            className="w-full flex items-center gap-3 px-4 py-2.5 text-left hover:bg-gray-50 transition-colors font-poppins text-sm text-gray-900"
          >
            <BarChart3 className="w-4 h-4 text-gray-600" />
            <span>Analytics</span>
          </button>

          <button
            onClick={handleShareProfile}
            className="w-full flex items-center gap-3 px-4 py-2.5 text-left hover:bg-gray-50 transition-colors font-poppins text-sm text-gray-900"
          >
            <Share2 className="w-4 h-4 text-gray-600" />
            <span>Share My Profile</span>
          </button>

          <div className="border-t border-gray-200 my-1" />

          <button
            onClick={handleLogout}
            className="w-full flex items-center gap-3 px-4 py-2.5 text-left hover:bg-red-50 transition-colors font-poppins text-sm text-red-600"
          >
            <LogOut className="w-4 h-4" />
            <span>Logout</span>
          </button>
        </div>
      )}
    </div>
  );
};

export default SettingsDropdown;


